.container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 20px;
  font-family: Arial, sans-serif;
}

.row {
  display: flex;
  flex-wrap: wrap;
}

.col-4 {
  width: 33.33%;
  padding: 20px;
  box-sizing: border-box;
}

.col-8 {
  width: 66.66%;
  padding: 20px;
  box-sizing: border-box;
}

.left-block {
  background-color: #f5f5f5;
  padding: 20px;
  border: 1px solid #ddd;
  margin-bottom: 20px;
}

.section-title {
  color: #007BFF;
}

.contact-info p {
  margin: 5px 0;
}

.contact-info a {
  text-decoration: none;
  color: #007BFF;
}

.contact-info a:hover {
  text-decoration: underline;
}

.download button {
  background-color: #007BFF;
  border: none;
  color: #fff;
  padding: 10px 20px;
  cursor: pointer;
}

.download button a {
  text-decoration: none;
  color: #fff;
}

.download button a:hover {
  text-decoration: underline;
}

.download {
  text-align: center;
}

.download button {
  display: inline-block;
}
/* CSS for screens smaller than 768px (adjust as needed) */
@media (max-width: 768px) {
  .row {
      flex-direction: column; /* Stack columns on top of each other */
  }
  .col-4, .col-8 {
      width: 100%; /* Take the full width on smaller screens */
  }
}

/* CSS for screens larger than 768px (adjust as needed) */
@media (min-width: 769px) {
  .row {
      flex-direction: row; /* Display columns next to each other */
  }
  .col-4 {
      width: 33.33%; /* Take one-third of the width on larger screens */
  }
  .col-8 {
      width: 66.67%; /* Take two-thirds of the width on larger screens */
  }
}